.schijnwerper {
    margin-top: 7rem;

    @media screen and (max-width: 62rem) {
        .text-intro {
            margin-top: 0.5rem;
        }

        h1 {
            padding: 0;
        }
    }

    .schijnwerper-article {
        display: flex;
        margin-top: $spacer;

        .schijnwerper-article-image-wrapper {
            box-sizing: border-box;
            position: relative;
            overflow: hidden;
            height: 100%;

            a:first-child img,
            a:first-child .imagePlaceholder {
                min-height: 300px;
                object-fit: cover;
                height: 100%;
                width: 100%;

                @media screen and (max-width: 62rem) {
                    min-height: 220px;
                }
            }
        }

        .schijnwerper-article-text-wrapper {

            .schijnwerper-title-line {
                background-color: $yellow;
                width: calc(100vw / 6 + 1rem);

                @media screen and (max-width: 62rem) {
                    width: calc(100vw/2 - 1rem);
                }

                height: 0.5rem;
                margin: 0.5rem 0;
                display: block;
                border-radius: 0 0.25rem 0.25rem 0;
            }

            .button {
                margin: 1rem 0;
            }
        }
    }
}